// 04 Reset Rules - 03 Links and linked content resets ======================== ***
// Reset rules adapted from normalize.css v4.1.1 | MIT License                  ***
// github.com/necolas/normalize.css                                             ***
// LINKS FIXES **************************************************************** ***
                    // Remove the gray background on active links in IE 10.     ***
                    // Remove gaps in links underline in iOS 8+ and Safari 8+.  ***
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
                    // Force hover outline color.                               ***
a:focus {
  outline: thin dotted $c-gray-border;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
                    // Remove the outline on focused links when also active     ***
                    // or hovered in all browsers.                              ***
a:active,
a:hover { outline: 0; outline-width: 0; }
// EMBEDDED CONTENT *********************************************************** ***
                    // Remove the border on images inside links in IE 10-.      ***
img { border-style: none; border: 0; }
                    // Clean up image sizing.                                   ***
img {
  max-width:              100%;
  height:                 auto;
  vertical-align:         middle;
  border:                 0;
  -ms-interpolation-mode: bicubic;
}
                    // Clean up image sizing for IE8.                           ***
.lt-ie8 img { width: auto\9; }
                    // Correct overflow not hidden in IE 9/10/11.               ***
svg:not(:root) { overflow: hidden; }
                    // Remove the gap between audio, canvas, iframes,           ***
                    // images, videos and the bottom of their containers.       ***
audio,
canvas,
iframe,
img,
svg,
video { vertical-align: middle; }
